@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap');
* {
  font-family: 'Rubik', Arial, sans-serif;
  color: #303030;
}

/* INSETS */
/* box-shadow:0px 0px 0px 1px #0e131809 inset; */

/* //////////// STRUCTURE ///////////// */
/* BODY */
body {
  background-color: #fefefe;
}

/* HEADER */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 48px;
  padding: 12px 16px 12px 36px;
}

header .button-container {
  display: flex;
}

/* MAIN */
main {
  width: 100vw;
  height: calc(100vh - 72px);
  background-color: #fafafa;
  position: relative;
  box-shadow:0px 0px 0px 1px #0e131809 inset;
  /* filter: drop-shadow(0 0 4px #e5e5e5) */
}

#tasks, #content {
  position: absolute;
  width: 100%;
  height: 100%;
  /* z-index: 3; */
  background-color: #fafafa;
  box-shadow:0px 0px 0px 1px #0e131809 inset;
  display: flex;
  /* filter: drop-shadow(0 0 2px #e5e5e5) */
}

#tasks {
  background-color: purple;
  padding: 36px;
  overflow: scroll hidden;

}

// #content {
//   background-color: peru;
// }

.tab {
  position: absolute;
  top: -60px;

  font-weight:500;
  font-size: 18px;
  padding: 18px 24px 24px 24px;
  border-radius: 12px 12px 0 0;
  box-shadow:0px 0px 0px 1px #0e131809 inset;

  color: #909090;
  background-color: #fefefe;
}

.tab:hover {
  cursor: pointer;
}

#tasks-tab {
  left: 135px;
  content: 'Tasks';
}

#content-tab {
  left: 247px;
  content: 'Content';
}

.tab.active {
  color: #303030;
  background-color: #fafafa;
}

/* covering the drop shadow with active tabs  */
#tasks-tab.active.tab::before, #content-tab.active.tab::before {
  position: absolute;
  left: 1.5px;
  bottom: 0;
  content: '';
  height: 8px;
  width: calc(100% - 3px);
  background-color: #fafafa;
  z-index: 5;
}

/* /////////////// KANBAN BOARD //////////////////// */
/* COLUMNS */
div.column {
    position: relative;
    min-width: 332px;
    max-width: 332px;
    // height: calc(100% - 36px);
    background-color: #eeeeee;

    margin: 0 36px;
    border-radius: 18px;
    padding: 18px;

    div.title {
        // position: sticky;
        // top: 0;
        z-index: 2;
        background-color: #fafafa;
        padding: 16px;
        // max-height: 2px;
        // margin: 18px;
        // border-radius: 12px;
        border-radius: 18px;
        display: flex;
        justify-content: space-between;
        h3 {
            font-weight: 500;
            font-size: 18px;
            color: #909090
        }
    }

    div.cards {
        width: 100%;
        height: calc(100% - 60px);
        margin-top: 14px;
        // margin-right: 29px;
        // padding-right: 15px;
        overflow-y: scroll;
        scroll-snap-type: proximity;
        article.card {
            z-index: 10;
            width: calc(100% - 36px);
            border-radius: 16px;
            background-color: #fefefe;
            padding: 16px;
            filter: drop-shadow(0 0 2px #30303018);
            margin: 16px 0;
            scroll-behavior: smooth;
            scroll-snap-align: start;
        }
    
        article.card:first-of-type {
            margin: 2px 0 0 0;
        }
        // article.card:last-of-type {
        //     margin: 0;
        // }
    }
}

div.column:first-of-type, div.column:last-of-type {
    margin: 0;
}


        /* ///////////// SCROLL BAR //////////// */
        div.cards::-webkit-scrollbar {
            width: 16px;               /* width of the entire scrollbar */
        }
    
        div.cards::-webkit-scrollbar-track {
            background: transparent;        /* color of the tracking area */
        }
    
        div.cards::-webkit-scrollbar-thumb {
            background-color: #cccccc;    /* color of the scroll thumb */
            border-radius: 20px;       /* roundness of the scroll thumb */
            // padding: 3px;
            // border-left: 0 solid transparent;
            border: 4px solid #eeeeee;  /* creates padding around scroll thumb */
        }

/* ///////////// DRAGGABLE //////////////// */
.draggable-source--is-dragging {
    // visibility: hidden;
    opacity: 0.2;
    border: 2px dashed #909090;
    color: transparent;
    * {
        color: transparent;
        background-color: transparent;
        border-color: transparent;
    }
}

.card.draggable-mirror {
    max-width: 296px;
}

/* ///////////// COMPONENTS /////////////// */

/* BUTTON FORMATTING */
.button {
  padding: 12px 18px;
  border-radius: 4px;
  font-weight: 500;
  font-size: 14px;

  display: flex;
  align-items: center;
  justify-content: space-around;
}

/* adding a bit of space between the buttons */
.button:not(:last-child) {
  margin-right: 16px;
}

.button:hover {
  cursor: pointer;
}

.button.primary {
  color: #fefefe;
  background-color: #09997F;
}

.button.secondary {
  background-color: #e5e5e5;
}

.button.primary:hover {
  background-color:#09997fdc
}

.button.secondary:hover {
  background-color:#dddddd
}

.button.primary.icon {
  display: none;
}

/* false stuff */
label, button#create-task-submit {
  display: block;
  margin-top: 1em;
}
  
/* /////////////////////////// RESPONSIVE CSS ////////////////////////////// */

@media only screen and (max-width: 600px) {
  /* LOGO/HOME-LINK */
  #home-link {
    display: none
  }

  header {
    height: 48px;
  }

  /* STRUCTURE */
  #task .tab {
    left: 16px;
  }

  #content .tab {
    left: 118px;
  }

  /* /////////// COMPONENTS //////////// */
  /* BUTTON */
  .button-container {
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    top: 12px;
    right: 12px;
  }

  .button.primary.icon {
    display: flex;
    width: 48px;
    height: 48px;
  }

  .button.primary.icon:hover {
    background-color:#09997fdc
  }

  .button:not(:last-child) {
    margin-right: 0;
    margin-bottom: 12px;
  }

  .button.create {
    background-color: #fefefe;
    color: #303030;
    visibility: hidden;
  }

  .button.create:hover {
    background-color: #e5e5e5;
  }

  
}